
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	/*margin-top: 60px;*/
}
html, body { font-size:12px; padding:0; margin:0;
}
header { display: -webkit-box; display: -ms-flexbox; display: flex; color: #fff; background-color: #3c4146; border-bottom-color: #202326;
}
header>ul { -webkit-box-flex: 1; -ms-flex: 1; flex: 1; list-style: none; margin: 0; padding: 0;
}
header>ul:after{ content:"";display:block;clear:both;
}
header>ul>li { float: left; padding: 0; margin: 0;
}
header>ul>li>a { display: block; padding: 1.5rem 1.5rem; color: #fff; text-decoration: none;
}
.user>li { float: right;
}
.user>li>img { width:32px; height:32px;
}
.logo { font-size: 1rem; padding: 0; color: #24292e; background: #444;
}

